import React from "react";
import './Button.css';

class Button extends React.Component{

    constructor(){
        super()
        this.state = {
            count : 0
        }
    }

    addCounter = () =>{
        this.setState(
            {
                count : this.state.count + 1
            }
        )
    }
    
     resetCounter() {
        this.setState({ count: 0 })
    }

    render(){
        return (
                <div>
                    <p><u>üêº <b>clicker counter = {this.state.count}</b> üêº</u></p>
                    
                    <button
                        id="button"
                        onClick={(e) => this.addCounter(e)}>
                        üêº Click me!! üêº
                    </button>
                    
                    <button
                        id="button"
                        onClick={(e) => this.resetCounter(e)}>
                         üêº Reset  üêº
                    </button>
                </div>
            
        )
    }
}

export default Button